@font-face {
  font-family: "Aquafina";
  src: url("./aguafina-script/AguafinaScript-Regular.ttf") format("truetype");
}
.App {
  text-align: center;
  position: relative;
}
.App .background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background-image: url("./assets/beach.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(5px);
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  height: 100px;
}

.App-menu {
  display: flex;
  flex-direction: row;
  position: fixed;
  padding: 5vh 5vw;
}

.App-menu__item {
  list-style-type: none;
  font-size: 20px;
  padding: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.App-menu__item a {
  transition: 0.3s;
  font-weight: 200;
}
.App-menu__item a:hover, .App-menu__item a.active {
  color: #177e89;
}

.homepage {
  display: flex;
  flex-direction: column;
}
.homepage .go {
  width: 10vw;
  height: 7vh;
  background: transparent;
  color: white;
  border: 1px solid white;
  transition: 0.3s;
  font-size: 1.2em;
  font-weight: 900;
}
.homepage .go:hover {
  background: white;
  color: #177e89;
}

.pageTitle {
  font-weight: 400;
  font-size: 5em;
  color: #f6a80a;
  color: white;
  font-family: "Aquafina";
  padding: 20vh 20vw;
}

/* ----ADMIN PANEL--- */
.Admin {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Admin__back {
  margin-top: 5px !important;
}
.Admin__bookingTable {
  width: 90vw !important;
}

.ReactModal__Overlay--after-open {
  display: flex;
  justify-content: center;
  align-items: center;
}

.Admin__Component {
  display: flex;
  justify-content: center;
}
.Admin__ComponentModal {
  width: 50vw;
  height: 50vh;
}
.Admin__ComponentModal form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.Admin__ComponentModal form > div {
  width: 50%;
}
.Admin__ComponentModal form button[type=submit] {
  margin-top: 15px;
}

/* ----CONTACT PANEL--- */
.Contact {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: calc(100vh - 100px);
}
.Contact__contactForm {
  width: 50vw;
}
.Contact__contactForm > div {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}

/* ----REGISTRATION/LOGIN PANEL--- */
.Registration, .Login {
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.Registration div, .Login div {
  width: 50vw;
}
.Registration button[type=submit], .Login button[type=submit] {
  margin-top: 15px;
}

